import { getPageList } from "@/api/modules/miniapp/page";
import FilterHeader from "@/components/FilterHeader";
import { Card, Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
interface DataType {
    key: React.Key;
    pid: string;
    source: string;
}

const PageList = () => {

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [pageTotal, setPageTotal] = useState(0);
    const [pageList, setPageList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: 'PID',
            dataIndex: 'pid',
            width: 100,
        },
        {
            title: 'source',
            dataIndex: 'source',
            width: 200,
        }
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initPageList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getPageList({ pageNum, pageSize, appId });
        if (result.data) {
            setPageTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.pageId,
                    pid: item.pid,
                    source: item.source,
                })
            });
            setPageList(data);
        }
    };

    useEffect(() => {
        initPageList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initPageList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: pageTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <Card style={{ marginTop: '16px' }} title="页面列表">{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={pageList} /></Card>
    </div>);
}

export default PageList;